body {
  padding: 20px;
  font-family: Arial, sans-serif;
}

.breed-select {
  margin-bottom: 20px;
  display: none;
}

.loader {
  display: none;
}

.loader.is-active {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #fff;
  border-bottom-color: #ff3d00;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.error {
  color: red;
  display: none;
}

.cat-info {
  align-items: center;
  display: flex;
}

.cat-info img {
  width: 500px;
  margin-right: 20px;
}

